import { list } from "@/config/examples/teachers";

export default {
  name: "teachers",
  data: () => ({
    searchText: "",
  }),
  computed: {
    teacherId() {
      return this.$route.params.id;
    },
    teachers() {
      return list.filter(teacher => teacher.name.toLowerCase().includes(this.searchText.toLowerCase()));
    }
  }
}
